$("._minus").click(function () {
    let current = getCurrentCount()
    if(current === 1){
        $(".buy-card__cart").removeClass("buy-card__cart--active")
        unsetCartBtnView()
        return
    }
    setCurrentCount(current-1)
    setCartBtnView(false)
})

$("._plus").click(function () {
    let current = getCurrentCount()
    setCurrentCount(current+1)
    setCartBtnView(true)
})

$("._add-cart").click(function () {
    $(".buy-card__cart").addClass("buy-card__cart--active")
    setCurrentCount(1)
    setCartBtnView(true)
})

function setCartBtnView(plus) {
    let cartItemsCount = getCurrentCount()
    if(cartItemsCount === 1){
        $(".header-middle .header__cart span").html("товар")
    } else if (cartItemsCount > 4){
        $(".header-middle .header__cart span").html("товаров")
    } else{
        $(".header-middle .header__cart span").html("товара")
    }
    $(".header__cart").attr("data-num",cartItemsCount)
    $(".header__cart").addClass("header__cart--active")

    if(plus) {
        $(".cart-info").addClass("cart-info--active")
        setTimeout(() => $(".cart-info").removeClass("cart-info--active"), 3000)
    }
}

function unsetCartBtnView() {
    $(".header__cart").attr("data-num",'')
    $(".header__cart").removeClass("header__cart--active")
    $(".header-middle .header__cart span").html("Корзина")
}


function getCurrentCount() {
    return +$(".buy-card__output").attr("data-output")
}

function setCurrentCount(count) {
    $(".buy-card__output").attr("data-output",count)
}
